.about-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  img {
    max-width: 250px;
  }
  @include tablet {
    justify-content: space-between;
    flex-wrap: nowrap;
    img {
      order: 1;
      margin-left: 1rem;
      margin-bottom: 3rem;
      max-width: 400px;
    }
  }
  .about-content {
    width: 100%;
    @include tablet {
      margin-right: 2rem;
    }
  }
}
  .who {
    margin-bottom: 4rem;
  }
  .interests {
    display: none;
    margin-bottom: 4rem;
    h3 {
      color: $color__text-main;
      font-family: $font__main;
      text-transform: none;
      text-align: left;
      font-size: 2.5rem;
    }
    .interests-list {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      @include tablet {
        justify-content: space-between;
      }
      li {
        margin-bottom: 2rem;
      }
    }
  }

.toggle {
  text-align: center;
  .about-button, .interests-button {
    cursor: pointer;
  }
  .about-button {
    font-weight: bold;
    padding-right: 3rem;
  }
  .interests-button {
    font-weight: lighter;
    padding-left: 3rem;
  }
}

.show-interests {
  .about-button {
    font-weight: lighter;
  }
  .interests-button {
    font-weight: bold;
  }
  .who {
    display: none;
  }
  .interests {
    display: block;
  }
}

.social-icons {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
  @include tablet {
    max-width: 600px;
  }
  a {
    width: 68px;
    height: 68px;
    display: block;
    border-radius: 100%;
    position: relative;
    @include tablet {
      width: 125px;
      height: 125px;
    }
  }

  .linkedin {
    background: 
      url(../../assets/linkedin-icon.svg) no-repeat, 
      url(../../assets/linkedin-hover.svg) no-repeat;
    background-size: contain;
    transition: background-image 0.5s;
    &:hover {
      background:url(../../assets/linkedin-hover.svg) no-repeat;
      background-size: contain;
    }
  }
  .instagram {
    background: 
      url(../../assets/ins.svg) no-repeat,
      url(../../assets/ins-hover.svg) no-repeat;
    background-size: contain;
    transition: background-image 0.5s;
    &:hover {
      background: url(../../assets/ins-hover.svg) no-repeat;
      background-size: contain;
    }
  }
  .medium {
    background: 
      url(../../assets/medium.svg) no-repeat,
      url(../../assets/medium-hover.svg) no-repeat;
    background-size: contain;
    transition: background-image 0.5s;
    &:hover {
      background: url(../../assets/medium-hover.svg) no-repeat;
      background-size: contain;
    }
  }
}
