.projects {
  padding: 3em 0em 0em;
}

.projects-list {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  li {
    @include tablet {
      position: relative;
      width: 100%;
      padding-bottom: 190px;
      display: flex;
      &:nth-child(even){
        justify-content: flex-end;
        background: url(../../assets/4.png) right bottom no-repeat;
        .project-info {
          left: 0;
        }
      }
      &:nth-child(odd){
        background: url(../../assets/left-side-decor.svg) left bottom no-repeat;
      }
      img {
        padding-bottom: 50px;
      }
    }
  }
  .project-info {
    color: white;
    background: $color__button;
    padding: 2em 2em;
    @include tablet {
      position: absolute;
      right: 0;
      bottom: 190px;
    }
    h3 {
      margin: 0;
      text-align: left;
      color: white;
      font: 1.6rem $font__main;
      font-weight: lighter;
      text-transform: none;
    }
    a {
      font-family: $font__header;
      width: 100%;
      display: block;
      padding: 1em;
      text-align: center;
      text-transform: uppercase;
      border: 2px solid white;
      text-decoration: none;
      color: white;
      border-radius: 50px;
      transition: all 1s ease;
      &:hover {
        background: white;
        color: $color__button;
      }
    }
    @include tablet {
      width: 35%;
      p {
        font-size: 1.6rem;
      }
    }
  }
    img {
    display: block;
    @include tablet {
      height: auto;
    }
  }
}