.why-me-mobile {
  li {
    margin-bottom: 85px;
    width: 100%;
    display: flex;
    justify-content: center;
  }
  @include tablet {
    display: none;
  }
}

.why-me-desktop {
  display: none;
  @include tablet {
    display: block;
    margin-bottom: 200px;
  }
}

.favorite-designers {
  .big-header {
    margin-bottom: 100px;
  }
}

.favorite-designers-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 100px;
  li {
    margin: 0 2rem 1rem;
  }
  img {
    max-width: 260px;
    margin-bottom: 1rem;
    font-size: 1.6rem;
  }
  @include tablet {
    justify-content: space-around;
  }
  h3 {
    color: $color__text-main;
    text-transform: none;
  }
}